@charset "utf-8";

// ==================================
//
// ==================================

//  -----------------------
	$colorType01: #FFF;	//
	$colorType02: #B21000;	//		
	$colorType03: #C1C1C1;	//	
	$colorType04: #D5D0BC;	//2	
	$colorType05: #F2F2F2;	//		
	$colorType06: #F7F5EC;	//		

	$color-link: #808080;	//
	$color-link-l: #ccc;	//
	$color-link-d: #000;	//

//  -----------------------
	$sansSerif: "","Meiryo"," ","Hiragino Kaku Gothic Pro"," Pro W3",sans-serif;
	$serif: "", YuMincho, " ProN W3", "Hiragino Mincho ProN", "HGE", " ", " ", serif;

	$font-weights: (
		Light:300,
		Regular:400,
		Medium:500,
		SemiBold:600,
		Bold:700,
		ExtraBold:800,
		Black:900,
	);
	@mixin fontType01($font-weight){
		font-family: "Noto Serif JP", serif;
		font-optical-sizing: auto;
		font-weight: map-get($font-weights,$font-weight);
		font-style: normal;
	}
	@mixin fontType02($font-weight){
		font-family: "Shippori Mincho B1", serif;
		font-weight: map-get($font-weights,$font-weight);
		font-style: normal;
	}
	@mixin fontType03($font-weight){
		font-family: "Zen Kaku Gothic New", sans-serif;
		font-weight: map-get($font-weights,$font-weight);
		font-style: normal;
	}
	@mixin fontType04($font-weight){
		font-family: "Inter", sans-serif;
		font-optical-sizing: auto;
		font-weight: map-get($font-weights,$font-weight);
		font-style: normal;
	}
	@mixin fontType05($font-weight){
		font-family: "EB Garamond", serif;
		font-optical-sizing: auto;
		font-weight: map-get($font-weights,$font-weight);
		font-style: normal;
	}

//  -----------------------
	$spaceSize: 1.8rem;

//  -----------------------
	$breakpoints: (
		xs:"print, screen and (min-width: 321px)",
		s:"print, screen and (min-width: 576px)",
		m:"print, screen and (min-width: 768px)",
		l:"print, screen and (min-width: 992px)",
		xl:"print, screen and (min-width: 1280px)",
		xxl:"print, screen and (min-width: 1800px)",
	);
	@mixin media($breakpoint){
		@media #{map-get($breakpoints,$breakpoint)}{
			@content;
		}
	}

// z-index  -----------------------
	$layer: (
		modal: 999,
		header: 20,
		tooltip: 10,
		default: 1
	);

// pxem  16-----------------------
	@function em($px, $base: 16) {
		$value: $px;
		$value: calc($px/$base) * 1em;
		@return $value;
	}

//  -----------------------
	$pageNameList: company, contact;